import React, {useEffect} from 'react'
import $ from 'jquery'

const Loader = () => {
    useEffect(() => {
        $(".pin").text("Loading")
        $(".loader-wrap").fadeOut(300, function () {
            $("#main").animate({
                opacity: "1"
            }, 300)
        })
    }, [])

    return (
        <div className="loader-wrap">
            <div className="pin"></div>
        </div>
    )
}

export default Loader
